import React from "react";

export const Modal = ({
    title,
    body,
    withCloseBtn,
    hasBgLayer,
    canCustomize,
    customStyles,
}) => {

    return (
        <div className={`flex h-screen items-center justify-center 
        ${canCustomize && hasBgLayer
                ? "bg-gray-400"
                : ""
            }`}>

            <div className="mb-10 p-3 text-xl">
                <h3>{body}</h3>
            </div>
            <div className="flex gap-1 text-white">
                <button
                 className={`block w-full  px-3 py-5 text-left ${canCustomize
                        ? [...Object.values(customStyles.cancelBtnStyles)].join(" ")
                        : "bg-gray-600 hover:bg-gray-700"
                    }`}>Confirm</button>
                <button className={`block w-full  px-3 py-5 text-left ${
              canCustomize
                ? [...Object.values(customStyles.delBtnStyles)].join(" ")
                : "bg-gray-600 hover:bg-gray-700"
            }`}> No</button>
            </div>

        </div>
    )
};

